import Figure from 'react-bootstrap/Figure';
import 'bootstrap/dist/css/bootstrap.css';

function Wildlife() {
    return (
        <div>
            <h1>Wildlife</h1>
            <Figure>
                <Figure.Image
                    width={100}
                    height={100}
                    src="https://github.com/brettbookbinder/gathertown/raw/master/fossil2%20(1).jpeg"/>
                <Figure.Caption>Name</Figure.Caption>
            </Figure>

            <Figure>
                <Figure.Image
                    width={100}
                    height={100}
                    src="https://github.com/brettbookbinder/gathertown/raw/master/fossil2%20(5).png"/>
                <Figure.Caption>Name</Figure.Caption>
            </Figure>
        </div>
    );
}

export default Wildlife;